@import './variable.scss';

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

html,
body,
#root {
    font-family: $basic-font;
    font-size: 10px;
    color: #333;
}

::selection {
    background: $sub-secondary;
    text-shadow: none;
}

// ::-webkit-scrollbar {
//     display: none;
// }

body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
legend,
input,
textarea,
button,
select {
    margin: 0;
    padding: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
    display: block;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

input,
textarea,
select,
button,
table {
    font-family: inherit;
    font-size: 1.2rem;
    color: #333;
}

body::-webkit-scrollbar {
    width: 8px;
    background-color: $sub-tertiary;
}

body::-webkit-scrollbar-thumb {
    background-color: $tertiary;
}

body::-webkit-scrollbar-track {
    background-color: $sub-tertiary;
}

a {
    cursor: pointer;
}

a:visited,
a:link {
    text-decoration: none;
    color: #fff;
}

/* 로딩 */
.bg-loder {
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.75);
    -webkit-tap-highlight-color: transparent;

    svg {
        color: $tertiary;
        animation: animation-rotate-img 4s linear infinite;
        transform-origin: 50% 50%;
        font-size: 10rem;
    }

    .ring {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 150px;
        height: 150px;
        background: transparent;
        border: 3px solid #3c3c3c;
        border-radius: 50%;
        text-align: center;
        line-height: 150px;
        font-size: 2rem;
        color: $tertiary;
        letter-spacing: 4px;
        text-transform: uppercase;
        box-shadow: 0 0 20px rgba(0, 0, 0, .5);
    }

    .ring:before {
        content: '';
        position: absolute;
        top: -3px;
        left: -3px;
        width: 100%;
        height: 100%;
        border: 3px solid transparent;
        border-top: 3px solid $tertiary;
        border-right: 3px solid $tertiary;
        border-radius: 50%;
        animation: animation-loading 2s linear infinite;
    }

    span {
        display: block;
        position: absolute;
        top: calc(50% - 2px);
        left: 50%;
        width: 50%;
        height: 4px;
        background: transparent;
        transform-origin: left;
        animation: animation-loading-A 2s linear infinite;
    }

    span:before {
        top: -20px;
        right: -11px;
        content: "";
        position: absolute;
        width: 16px;
        height: 16px;
        border-radius: 20%;
        border-left: 4px solid $tertiary;
        border-top: 4px solid $tertiary;
        background: transparent;
        transform: rotate(224deg);
    }
}


/* 가로모드 모바일 디바이스 (가로 해상도가 100px 보다 큰 화면에 적용) */
@media (min-width: 100px) {
    body {
        div.container {
            font-size: 1.6rem;
            min-width: 320px;
            font-weight: 300;
            position: relative;
            line-height: 1.6;
            overflow-x: hidden;
            -webkit-font-smoothing: antialiased;
            text-rendering: optimizeLegibility;

            header.top-nav {
                transition: all ease 0.5s;
                position: absolute;
                top: 0;
                width: 100%;
                z-index: 4;

                .nav__container {
                    margin-right: auto;
                    margin-left: auto;
                    padding-left: 15px;
                    padding-right: 15px;

                    nav {
                        float: right;
                        color: #fff;

                        ul {
                            &>li {
                                float: left;
                                font-weight: 600;
                                font-size: 1.1rem;
                                line-height: 14px;
                                text-transform: uppercase;
                                background-color: transparent;
                                padding: 20px 0px;
                                letter-spacing: 0.7px;
                                margin-right: 15px;
                                cursor: pointer;
                                transition: font-size ease 0.8s;

                                & a:hover {
                                    transition: all ease 0.3s;
                                    color: $tertiary;
                                }
                            }
                        }
                    }
                }

                &.fixed {
                    position: fixed;
                    background-color: $tertiary;

                    nav {
                        ul {
                            &>li {
                                padding: 10px 0px;
                                font-size: 1rem;

                                a {
                                    color: #fff;
                                }
                            }
                        }
                    }
                }
            }

            div.top {
                position: relative;
                overflow: hidden;
                padding: 0;
                vertical-align: middle;
                background: $sub-tertiary; //$primary;
                text-align: center;
                height: calc(100vh);

                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 100%;
                    transition: all 0.7s ease;
                    height: 100%;
                    top: 0;
                    right: 0;
                    left: 0;
                    background-color: rgba(12, 12, 12, 0.73);
                    z-index: 1;
                }

                .top__container {
                    height: 100%;
                    transition: background-color 0.3s;

                    .main {
                        position: relative;
                        z-index: 3;
                        display: table;
                        width: 100%;
                        height: 100%;

                        .title {
                            display: table-cell;
                            vertical-align: middle;
                            color: #fff;

                            h3 {
                                transition: font-size 0.8s;
                                font-size: 2rem;
                                font-weight: 900;
                                letter-spacing: 5px;
                                text-transform: uppercase;

                                span {
                                    color: $tertiary;
                                }

                                animation-fill-mode: both;
                                animation-duration: 800ms;
                                animation-delay: 300ms;
                                animation-iteration-count: 1;
                                animation-name: animation-main-title;
                            }

                            .sub-title {
                                transition: font-size 0.8s;
                                font-size: 1rem;
                                font-weight: 900;
                                animation-fill-mode: both;
                                animation-duration: 800ms;
                                animation-delay: 1200ms;
                                animation-iteration-count: 1;
                                animation-name: animation-main-title;
                            }

                            .btn-area {
                                margin-top: 50px;
                                margin-bottom: 50px;

                                .btn-more {
                                    position: relative;
                                    transition: font-size 0.8s;
                                    font-size: 1rem;
                                    padding: 10px;
                                    border: 2px solid $tertiary;
                                    display: inline-block;
                                    font-weight: 600;

                                    animation-fill-mode: both;
                                    animation-duration: 1000ms;
                                    animation-delay: 1800ms;
                                    animation-iteration-count: 1;
                                    animation-name: animation-main-title;

                                    &:hover {
                                        background-color: $tertiary;
                                        transition: all ease 0.5s;
                                    }
                                }

                            }

                            .btn-arrow-area {
                                display: flex;
                                justify-content: center;

                                .btn-arrow {
                                    transition: border 0.8s;
                                    opacity: 0;
                                    width: 20px;
                                    height: 20px;
                                    border-left: 4px solid #fff;
                                    border-bottom: 4px solid #fff;
                                    transform: rotate(-45deg);
                                    animation-delay: 2500ms;
                                    animation-name: animation-arrow-down;
                                    animation-duration: 1.5s;
                                    animation-iteration-count: infinite;
                                    box-sizing: border-box;
                                }
                            }
                        }
                    }


                    &.backgroundColorChange {
                        background-color: #000;

                        .main {
                            h3 {
                                font-size: 1rem;
                            }

                            .sub-title {
                                font-size: 0.8rem;
                            }

                            .btn-area {
                                .btn-more {
                                    padding: 10px;
                                    font-size: 0.8rem;
                                    border: 1px dotted $tertiary;
                                }
                            }

                            .btn-arrow-area {
                                .btn-arrow {
                                    width: 10px;
                                    height: 10px;
                                }
                            }
                        }
                    }
                }
            }

            div.intro {
                height: 250px;
                background-image: $img-sky;
                background-size: cover;
            }

            section.history {
                position: relative;
                text-align: center;
                padding-right: 50px;
                padding-left: 50px;
            }

            section.contents {
                min-height: 1000px;
                text-align: center;
                padding-right: 50px;
                padding-left: 50px;

            }

            section.tail {
                height: 200px;
                background: $sub-tertiary;
            }

            footer {
                background-color: #272729;
                padding-top: 20px;
                padding-bottom: 20px !important;
                color: #fff;
                font-size: 1.2rem;
                font-weight: 500;

                .copyright {
                    text-align: center;
                }
            }

            #btn__top {
                cursor: pointer;
                display: none;
                flex-flow: column;
                justify-content: center;
                position: fixed;
                bottom: 30px;
                right: 30px;
                color: #fff;
                width: 30px;
                height: 30px;
                text-align: center;
                z-index: 3;
                border-radius: 50px;
                // background-color: $tertiary;

                svg {
                    width: 28px;
                }

                &.view {
                    display: flex;
                }
            }

        }
    }
}

/* 태블릿 디바이스 (가로 해상도가 768px 보다 큰 화면에 적용) */
@media (min-width: 768px) {}

/* 테스크탑 (가로 해상도가 992px 보다 큰 화면에 적용) */
@media (min-width: 992px) {}

/* 큰화면 데스크탑 (가로 해상도가 1200px 보다 큰 화면에 적용) */
@media (min-width: 1200px) {
    body {
        div.container {
            .nav__container {
                nav {
                    ul {
                        &>li {
                            padding: 40px 0px;
                            font-size: 1.4rem;
                        }
                    }
                }
            }

            &.fixed {

                nav {
                    ul {
                        &>li {
                            padding: 20px 0px;
                            font-size: 1.2rem;
                        }
                    }
                }
            }

            div.top {
                .top__container {
                    .main {
                        .title {
                            h3 {
                                font-size: 8rem;
                            }

                            .sub-title {
                                font-size: 4rem;
                            }

                            .btn-area {
                                .btn-more {
                                    font-size: 3rem;
                                    padding: 30px;
                                }
                            }

                            .btn-arrow-area {
                                .btn-arrow {
                                    width: 40px;
                                    height: 40px;
                                }
                            }
                        }
                    }

                    &.backgroundColorChange {
                        .main {
                            h3 {
                                font-size: 4rem;
                            }

                            .sub-title {
                                font-size: 2rem;
                            }

                            .btn-area {
                                .btn-more {
                                    font-size: 1.2rem;
                                }
                            }


                            .btn-arrow-area {
                                .btn-arrow {
                                    width: 20px;
                                    height: 20px;
                                }
                            }
                        }
                    }

                }
            }

            div.intro {
                height: 500px;
            }


            section.tail {
                height: 400px;
            }

            #btn__top {
                width: 60px;
                height: 60px;

                svg {
                    width: 48px;
                }

                &.view {
                    display: flex;
                }
            }
        }
    }
}